.portfolio-header {
  overflow: visible;
  background-color: white;
}

.portfolio-navigation-container {
  /* Simple hack to make the overflow happen to the left instead... */
  direction: rtl;
  -ms-flex-order: 1;
      order: 1;
  width: 500px;
  -webkit-transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1),
      width 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  -o-transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1),
      width 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1),
      width 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.portfolio-search-box.is-focused + .portfolio-navigation-container {
  opacity: 0;
  width: 100px;
}

.portfolio-search-box {
  -ms-flex-order: 2;
      order: 2;
  margin-left: 16px;
  margin-right: 16px;
}

.portfolio-navigation .mdl-navigation__link {
  display: inline-block;
  height: 60px;
  line-height: 68px;
  background-color: transparent !important;
  border-bottom: 4px solid transparent;
}

.portfolio-navigation .mdl-navigation__link:hover {
  border-bottom: 4px solid #8bc34a;
}

.portfolio-navigation {
  /* ... and now make sure the content is actually LTR */
  direction: ltr;
  -ms-flex-pack: end;
      justify-content: flex-end;
  width: 800px;
}

.portfolio-logo-image {
  height: 64px;
  width: 64px;
}

.portfolio-header .mdl-menu__container {
  z-index: 50;
  margin: 0 !important;
}

.portfolio-header .material-icons {
  color: #767777 !important;
}

.portfolio-header .mdl-layout__drawer-button {
  background: transparent;
  color: #767777;
}

.portfolio-header .mdl-navigation__link {
  color: #757575;
  font-weight: 700;
  font-size: 14px;
}

.portfolio-mobile-title {
  display: none !important;
}

@media (max-width: 940px) {
  .portfolio-navigation-container {
    display: none;
  }

  .portfolio-title {
    display: none !important;
  }

  .portfolio-mobile-title {
    display: block !important;
    position: absolute;
    left: calc(50% - 70px);
    -webkit-transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    -o-transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  }
}


