.portfolio-spotlight {
  position: relative;
  height: 800px;
  width: auto;
  background-color: #f3f3f3;
  background: url('/images/dem-bgd.png') center 50% no-repeat;
  background-size: cover;
  border-bottom: 1px solid #D9D9D9;
}

.portfolio-fab {
  position: absolute;
  right: 20%;
  bottom: -26px;
  z-index: 3;
  background: #6100a0 !important;
  color: #fff !important;
}

@media (max-width: 940px) {
  .portfolio-spotlight {
    height: 350px;
  }

  .portfolio-fab {
    display: none;
  }
}



