.portfolio-screen-section {
  position: relative;
  padding-top: 60px;
  padding-bottom: 80px;
}

.portfolio-more-section {
  padding: 80px 0;
  max-width: 1044px;
  margin-left: auto;
  margin-right: auto;
}

.portfolio-more-section .portfolio-section-title {
  margin-left: 12px;
  padding-bottom: 24px;
}

.portfolio-card-container .mdl-card__media {
  overflow: hidden;
  background: transparent;
}

.portfolio-card-container .mdl-card__media img {
  width: 100%;
}

.portfolio-card-container .mdl-card__title {
  background: transparent;
  height: auto;
}

.portfolio-card-container .mdl-card__title-text {
  color: black;
  height: auto;
}

.portfolio-card-container .mdl-card__supporting-text {
  height: auto;
  color: black;
  padding-bottom: 56px;
}

.portfolio-card-container .mdl-card__actions {
  position: absolute;
  bottom: 0;
}

.portfolio-card-container .mdl-card__actions a {
  border-top: none;
  font-size: 16px;
}


